import React from "react";
import { ReactNode, useState } from "react";
import styles from './css/CreateGroupForm.module.css'

import {
    Form,
    FormGroup,
    Label,
    Col,
    Input,
    FormText,
    Button,
    CloseButton
  } from 'reactstrap';

function EditGroupForm({editGroup, toggleEditGroup, group}): ReactNode {
  console.log(group, 'group at edit')
    const [images, setImages] = useState([])


    const initialState = {
        name:group.group.name,
        fish_species: group.group.fish_species,
        area:group.group.area,
        description:group?.group.description,
    };
    const [formData, setFormData] = useState(initialState);


    function handleChange(evt) {
      const { name, value, type, files } = evt.target;
      if (type === "file") {
          setFormData(fData => ({
              ...fData,
              [name]: files[0]
          }));
      } else {
          setFormData(fData => ({
              ...fData,
              [name]: value
          }));
      }
  }


    function handleSave(evt) {
        evt.preventDefault();
        editGroup(formData);
        setFormData(initialState);

    }

    const handleAddImage = () => {
        if (images.length < 5) {
          setImages([...images, null]);
        }
      };

    return (
        <div>
        <Form onSubmit={handleSave} className={`${styles.form} border border-primary rounded`} >
          <CloseButton onClick={toggleEditGroup}/>
          <h1>Edit Group</h1>
        <FormGroup row>
          <Label
            for="name"
            sm={2}
          >
            Name
          </Label>
          <Col sm={10}>
            <Input
              id="name"
              name="name"
              placeholder={formData.name}
              value={formData.name}
              type="text"
              onChange={handleChange}
            />
          </Col>
        </FormGroup>
        <FormGroup row>
          <Label
            for="description"
            sm={2}
          >
            Description
          </Label>
          <Col sm={10}>
            <Input
              id="description"
              name="description"
              placeholder={formData.description}
              value={formData.description}
              type="text"
              onChange={handleChange}
            />
          </Col>
        </FormGroup>
        <FormGroup row>
          <Label
            for="fish_species"
            sm={2}
          >
            Fish Species
          </Label>
          <Col sm={10}>
            <Input
              id="fish_species"
              name="fish_species"
              placeholder={formData.fish_species}
              value={formData.fish_species}
              type="text"
              onChange={handleChange}
            />
          </Col>
        </FormGroup>
        <FormGroup row>
          <Label
            for="area"
            sm={2}
          >
            Area
          </Label>
          <Col sm={10}>
            <Input
              id="area"
              name="area"
              placeholder={formData.area}
              value={formData.area}
              type="text"
              onChange={handleChange}
            />
          </Col>
        </FormGroup>
        {images.map((image, index) => (
        <div key={index}>
          <FormGroup row>
          <Label
            for={`image_${index+1}`}
            sm={2}
          >
            Image
          </Label>
          <Col sm={10}>
            <Input
              id={`image_${index+1}`}
              name={`image_${index+1}`}
              type="file"
              onChange={handleChange}
            />
          </Col>
        </FormGroup>
        </div>
      ))}

        <Button>
        Submit
      </Button>

      </Form>
      <Button onClick={handleAddImage}>+</Button>
      </div>

    );
}

export default EditGroupForm